
import { Options, Vue, setup } from 'vue-class-component';
import { Watch } from 'vue-property-decorator';

import {
  useAppointmentIntervals,
  useCheckUserProfile,
  useConsultationInfo,
  useSwitcher,
} from '@/composable';

import { DoctorIntervalDto, DoctorIntervalGroupDto, DoctorIntervalValueDto } from '@/types/models';
import { CalendarDateChangeOutput } from '@/ui/molecules/RCalendar/types';
import { DayVariant, DayVariantsMapper } from '@/pages/DoctorAppointment/types';

import { getDayVariantsMapper } from '@/pages/DoctorAppointment/dictionaries';

import { ModalCalendar, ToggleBadge } from '@/ui/molecules';
import { RBadge, TimeBadge, RSpinner } from '@/ui/atoms';
import { BadgesWithContent } from '@/ui/organisms';

@Options({
  components: {
    RBadge,
    TimeBadge,
    RSpinner,
    ModalCalendar,
    BadgesWithContent,
    ToggleBadge,
  },
})
export default class DoctorAppointment extends Vue {
  appointmentIntervals = setup(useAppointmentIntervals);

  checkUserProfile = setup(useCheckUserProfile);

  consultationInfo = setup(useConsultationInfo);

  switcher = setup(() => useSwitcher<DayVariant>('today'));

  isModalCalendarShow = false;

  otherDate: string | null = null;

  get isToday(): boolean {
    return this.switcher.isActive('today');
  }

  get isTomorrow(): boolean {
    return this.switcher.isActive('tomorrow');
  }

  get isOtherDate(): boolean {
    return this.switcher.isActive('otherDate');
  }

  get daysVariantMapper(): DayVariantsMapper {
    return getDayVariantsMapper(this.otherDate);
  }

  get appointmentInterval(): DoctorIntervalDto | undefined {
    if (this.switcher.activeSwitch) {
      return this.appointmentIntervals
        .getIntervalByDay(this.daysVariantMapper[this.switcher.activeSwitch]);
    }

    return undefined;
  }

  get hasTodayIntervals(): boolean {
    return !!this.appointmentIntervals
      .getIntervalByDay(this.daysVariantMapper.today);
  }

  get hasTomorrowIntervals(): boolean {
    return !!this.appointmentIntervals
      .getIntervalByDay(this.daysVariantMapper.tomorrow);
  }

  get hasOtherDateIntervalsOnly(): boolean {
    return !this.hasTodayIntervals && !this.hasTomorrowIntervals;
  }

  get hasActiveAppointmentIntervalGroups(): boolean {
    return !!this.appointmentIntervalGroups.length;
  }

  get appointmentIntervalGroups(): DoctorIntervalGroupDto[] {
    return this.appointmentInterval?.groups ?? [];
  }

  get availableDates(): Date[] {
    return this.appointmentIntervals.appointmentIntervals.data
      .map(({ date }) => new Date(date));
  }

  handleIntervalChoose(doctorInterval: DoctorIntervalValueDto): void {
    const { saveIntervalToStoreAndBrowserStorage } = this.consultationInfo.consultationInfoStore;

    saveIntervalToStoreAndBrowserStorage(doctorInterval);
    this.appointmentIntervals.chooseIntervalOrUpdateIntervals(doctorInterval.id);
  }

  handleOtherDateSwitch(): void {
    this.isModalCalendarShow = true;
  }

  handleDateChange(otherDate: CalendarDateChangeOutput): void {
    if (otherDate) {
      this.otherDate = otherDate;
      this.switcher.setActiveSwitch('otherDate');
    }
  }

  // todo escape imperative switching
  @Watch('appointmentIntervals.appointmentIntervals.data')
  onAppointmentIntervalsChange(): void {
    if (this.hasTodayIntervals) {
      this.switcher.setActiveSwitch('today');
    } else if (this.hasTomorrowIntervals) {
      this.switcher.setActiveSwitch('tomorrow');
    }
  }
}
