
import { Options, setup, Vue } from 'vue-class-component';
import { useRoute } from 'vue-router';

import { usePagedData } from '@/composable';

import { AsyncCb } from '@/store/stores/pagedData/types';
import { DoctorCommentDto } from '@/types/models';

import DoctorAppointmentServiceInstance from '@/services/doctors';

import { RSeparator, RSpinner } from '@/ui/atoms';
import { ReviewCard, ReviewDefaultCard } from '@/ui/organisms';

@Options({
  components: {
    ReviewCard,
    ReviewDefaultCard,
    RSeparator,
    RSpinner,
  },
})
export default class Reviews extends Vue {
  reviews = setup(() => {
    const route = useRoute();

    function asyncCbWrapper(page: number, count: number): AsyncCb<DoctorCommentDto> {
      const { getDoctorComments } = DoctorAppointmentServiceInstance;

      return (): ReturnType<typeof getDoctorComments> => getDoctorComments({
        params: {
          doctorId: route.params.doctorId as string,
          Page: page,
          Count: count,
        },
      });
    }

    return usePagedData<DoctorCommentDto>('reviews', asyncCbWrapper);
  });
}
