
/* eslint-disable @typescript-eslint/camelcase */
import { Options, Vue, setup } from 'vue-class-component';

import { useDoctorAppointment } from '@/composable';

import { SelectedTab } from './types';

import { getSpecialityFromBrowserStorage } from '@/utils';

import { RCard, RCardActions, RCardSection } from '@/ui/molecules';
import {
  RImg, RSeparator, RTabs, RTab, RBadge, RSpinner,
} from '@/ui/atoms';
import { DefaultCard } from '@/ui/organisms';

import AppointmentIntervals
  from '@/pages/DoctorAppointment/AppointmentIntervals/AppointmentIntervals.vue';
import Reviews
  from '@/pages/DoctorAppointment/Reviews/Reviews.vue';
import AboutDoctor
  from '@/pages/DoctorAppointment/AboutDoctor/AboutDoctor.vue';

@Options({
  components: {
    AboutDoctor,
    AppointmentIntervals,
    DefaultCard,
    RBadge,
    RCard,
    RCardActions,
    RCardSection,
    RImg,
    RSeparator,
    RSpinner,
    RTab,
    RTabs,
    Reviews,
  },
})
export default class DoctorAppointment extends Vue {
  doctorAppointment = setup(useDoctorAppointment);

  selectedTab: SelectedTab = 'appointmentIntervals';

  handleTabUpdate(tabName: SelectedTab): void {
    if (tabName === 'reviews') {
      this.$analyticsUtils.pushEvent('doctor_profile_reviews', {
        eventLabel: getSpecialityFromBrowserStorage()?.specialityName ?? '',
        eventCategory: 'telemed',
        doctor_guid: this.$route.params.doctorId as string,
      });
    }

    if (tabName === 'about') {
      this.$analyticsUtils.pushEvent('doctor_profile_about', {
        eventLabel: getSpecialityFromBrowserStorage()?.specialityName ?? '',
        eventCategory: 'telemed',
        doctor_guid: this.$route.params.doctorId as string,
      });
    }
  }

  mounted(): void {
    this.$analyticsUtils.pushEvent('doctor_profile_view', {
      eventLabel: getSpecialityFromBrowserStorage()?.specialityName ?? '',
      eventCategory: 'telemed',
      doctor_guid: this.$route.params.doctorId as string,
    });
  }
}
